import React from "react";
import NilssongamesSnake from "nilssongames-snake";
import { Browser } from "react-window-ui";
const Game = props => {
  if (window.innerWidth >= 992) {
    return (
      <center>
      <div>
      <br/>
        <Browser style={{width:'600px', padding:'20px', backgroundColor:'#1d1d1a'}}>
        <br/>
        <h1 style={{color:'white'}}>Desktop users press enter to start, and arrow keys to play!"</h1>
        <NilssongamesSnake style={{ width: "50px", height: "100px" }} />
        </Browser>
      </div>
      </center>
    );
  }
  else {
    return (
      <div>
      </div>
    )
  }
};

export default Game;
