import React, { Component } from "react";
import axios from "axios";
import "../css/media.css";
import { Container, Row, Col, Button, Card } from "reactstrap";
import "bootstrap/dist/css/bootstrap.min.css";
import MediaCarousel from "./../../media-carousel/scripts/media_carousel.js";
import FooterSessionButtons from "./../../footer-session-buttons/scripts/footer_session_buttons.js";
import TextTerminal from "./../../text-terminal/scripts/text_terminal.js";
import JsonTerminal from "./../../json-terminal/scripts/json_terminal.js";
import AlertWithSharing from "./../../alert-with-share/scripts/alert_with_share.js";
import AlertWithBadge from "./../../alert-with-badge/scripts/alert_with_badge.js";
import {
  AWS_BASE_URL,
  APPLICATION_BASE_URL,
  APPLICATION_COMPLETE_REFRESH_URL,
  SHARE_SESSION_URL
} from "./../../../api-endpoints/scripts/api-endpoints.js";

var self;

class DisplayResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      projectId: props.projectId,
      _session: props.session,
      get_json_link: AWS_BASE_URL + "/getLinks.php?session_folder_name=",
      json_data_media: [],
      output_text_file_link:
      AWS_BASE_URL+"/getTextOutput.php?session_folder_name=" +
      props.session +
      "&uniqueId=" +
      props.projectId,
      generate_json_link:
        AWS_BASE_URL + "/generateJSON.php?session_folder_name=" + props.session+"&uniqueId="+props.projectId,
      generate_text_link:
        AWS_BASE_URL +
        "/getTextOutput.php?session_folder_name=" +
        props.session+"&uniqueId="+props.projectId,
      json_output_data: [],
      text_output_data: [],
      share_link: SHARE_SESSION_URL + props.session + "&q="+props.projectId + "&r=" + this.props.match.params.checkpoint,
      from_shared_link: false,
      add_more_link: APPLICATION_BASE_URL + "/" + props.session
    };
    if (!this.state._session) {
      this.state = {
        projectId: this.props.match.params.projectId,
        _session: this.props.match.params.id,
        get_json_link: AWS_BASE_URL + "/getLinks.php?session_folder_name=",
        json_data_media: [],
        output_text_file_link:
        AWS_BASE_URL+"/getTextOutput.php?session_folder_name=" +
        this.props.match.params.id +
        "&uniqueId=" +
        this.props.match.params.projectId,
        generate_json_link:
          AWS_BASE_URL +
          "/generateJSON.php?session_folder_name=" +
          this.props.match.params.id+"&uniqueId="+this.props.match.params.projectId,
        generate_text_link:
          AWS_BASE_URL +
          "/getTextOutput.php?session_folder_name=" +
          this.props.match.params.id+"&uniqueId="+this.props.match.params.projectId,
        json_output_data: [],
        text_output_data: [],
        share_link:
          SHARE_SESSION_URL + this.props.match.params.id + "&q="+this.props.match.params.projectId + "&r=" + this.props.match.params.checkpoint,
        from_shared_link: true,
        add_more_link: APPLICATION_BASE_URL + "/" + this.props.match.params.projectId +"/" + this.props.match.params.checkpoint+ "/"+this.props.match.params.id
      };
    }

    self = this;
    axios
      .get(this.state.get_json_link + this.state._session + "&uniqueId="+this.state.projectId)
      .then(function(response) {
        self.setState({
          json_data_media: response.data
        });
      });
    axios.get(this.state.generate_text_link).then(function(response) {
      self.setState({
        text_output_data: response.data
      });
    });
    axios.get(this.state.generate_json_link).then(function(response) {
      self.setState({
        json_output_data: response.data
      });
    });
  }

  addMore() {
    window.location.href = "http://champsinc.github.io/application/#/"+this.props.match.params.projectId+"/"+this.props.match.params.checkpoint+"/"+this.props.match.params.id;
  }

  checkForContinueSessionButtons() {
    if (this.state.json_data_media) {
      if (this.state.json_data_media.length > 0) {
        if (this.state.from_shared_link) {
          return (
            <div>
              <br />
              <FooterSessionButtons
                button_text={"Start Afresh"}
                add_more_link={APPLICATION_COMPLETE_REFRESH_URL+"/"+this.state.projectId+"/"+this.props.match.params.checkpoint}
              />
              <div>
                <br />
                <center>
                  <Button color="primary" onClick={() => this.addMore()}>
                    Add some other image(s)/video(s)
                  </Button>
                </center>
              </div>
            </div>
          );
        }
      }
    }
  }

  getSessionNotExistMessage() {
    return (
      <div>
        <br />
        <Container>
          <center>
            <AlertWithBadge
              session_no={this.state._session}
              color={"danger"}
              alert_message={"Session Number Does Not Exist!!"}
            />
            <FooterSessionButtons
              button_text={"Start Afresh"}
              add_more_link={APPLICATION_COMPLETE_REFRESH_URL}
            />
          </center>
        </Container>
      </div>
    );
  }

  checkAndGetOutput() {
    if (this.state.json_data_media) {
      if (this.state.json_data_media.length > 0) {
        return (
          <div>
            <Container>
              <AlertWithSharing
                session_no={this.state._session}
                link={this.state.share_link}
                message={
                  "Hey, view my test media for objects detected! You can add your own to it too! Happy detecting and spreading the word!!"
                }
              />
            </Container>

            <Container>
            <Card style={{backgroundColor:'rgba(222,222,222,0.3)',borderRadius:'10px'}}>
              <MediaCarousel mediaData={this.state.json_data_media} />
              </Card>
            </Container>

            <Container>
              <Row>
                <Col xs="12" sm="6">
                  <TextTerminal
                    output_text_link={this.state.output_text_file_link}
                    output_text_data={this.state.text_output_data}
                  />
                </Col>

                <Col sm="6">
                  <JsonTerminal
                    generate_json_link={this.state.generate_json_link}
                    output_json_data={this.state.json_output_data}
                  />
                </Col>
              </Row>
            </Container>
          </div>
        );
      } else {
        return <div>{this.getSessionNotExistMessage()}</div>;
      }
    } else {
      return <div>{this.getSessionNotExistMessage()}</div>;
    }
  }

  render() {
    return (
      <div>
        {this.checkAndGetOutput()}
        {this.checkForContinueSessionButtons()}
        <br />
        <br />
      </div>
    );
  }
}

export default DisplayResults;
