import React, { Component } from "react";
import Image from "react-image-resizer";
import {CAROUSEL_BACKGROUND_URL} from "./../../../api-endpoints/scripts/api-endpoints.js";

class MediaComponent extends Component {
  getInnerJSX() {
    if (this.props.data.type === "image") {
      console.log(this.props.data.type);
      return (
        <center>
          <div
            style={{
              backgroundImage:
                "url("+CAROUSEL_BACKGROUND_URL+")"
            }}
            width={365}
            height={365}
          >
            <Image
              src={this.props.data.src}
              width={360}
              height={360}
              alt={this.props.data.altText}
            />
          </div>
        </center>
      );
    } else if (this.props.data.type === "video") {
      return (
        <center>
          <div
            style={{
              border: "2px solid #666666",
              backgroundImage:
                "url("+CAROUSEL_BACKGROUND_URL+")"
            }}
            width={365}
            height={365}
          >
            <video controls loop autoplay="true" muted="true">
              <source src={this.props.data.src} type="video/mp4" />
              __Your browser does not support the video tag__.
            </video>
          </div>
        </center>
      );
    }
  }
  render() {
    return <div>{this.getInnerJSX()}</div>;
  }
}

export default MediaComponent;
